      .left-menu-inner {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .menu-item {
          padding: 0 10px;

          .menu-icon {
            padding: 0 16px;
          }
        }
      }
    }

    .right-content {
      padding: 0;
    }
  }
}
